@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);



* {
    box-sizing: border-box;
}

body {
    background-color: black;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: 'Montserrat', sans-serif;
    margin-bottom: 100px;
}

#app {
    width: 100%;
    max-width: 768px;
}

.err {
    color: white;
}

form {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin-bottom: 15px;
}

    form input {
        border: 2px solid #aaa;
        color: #333;
        font-size: 14px;
        font-family: Lato;
        padding: 10px 15px;
        height: 40px;
        width: 300px;
        max-width: 100%;
    }

    form button {
        border: 1px solid #aaa;
        background-color: crimson;
        color: black;
        padding: 0 12px;
        height: 40px;
    }

        form button:focus {
            outline: none;
        }

        form button:active {
            opacity: 0.7;
        }

.filmContainer {
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 16px 24px 2px rgba(0, 0, 0, 0.14), 0 6px 30px 5px rgba(0, 0, 0, 0.12), 0 8px 10px -7px rgba(0, 0, 0, 0.2);
    overflow: hidden;
    display: flex;
    min-height: 420px;
    margin: 0 auto 50px;
    width: 80%;
    padding: 0;
}
.h11 {
    color: white;
    text-align: center;
}
.imgContainer {
    -webkit-clip-path: circle(90% at 30px 30px);
    clip-path: circle(90% at 30px 30px);
    flex: 4 1;
}

.backImage {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top left;
    width: 100%;
    height: 100%;
}

.filmInfo {
    display: flex;
    flex: 3 1;
    flex-direction: column;
    align-items: flex-end;
    justify-content: space-between;
    text-align: right;
    padding: 30px 20px 30px 0;
    z-index: 1;
}

    .filmInfo h2 {
        text-transform: uppercase;
        margin: 0;
        padding: 0 0 10px;
        border-bottom: 2px solid rgba(0, 0, 0, 0.3);
    }

    .filmInfo h1 {
        font-weight: bold;
        font-size: 36px;
        margin: 20px 0 0;
    }

    .filmInfo h4 {
        margin: 20px 0 10px;
    }

    .filmInfo p {
        font-size: 14px;
        margin: 10px 0;
        width: 130%;
    }

    .filmInfo .tagsCont {
        position: relative;
        width: 225%;
    }

        .filmInfo .tagsCont span {
            border: 2px solid black;
            border-radius: 4px;
            display: inline-block;
            font-size: 12px;
            padding: 3px 10px;
            margin: 5px;
        }



@media (max-width: 768px) {
    .imgContainer {
        flex: 5 1;
    }
}

@media (max-width: 700px) {
    .imgContainer {
        -webkit-clip-path: circle(80% at 45px 70px);
        clip-path: circle(80% at 45px 70px);
    }
}

@media (max-width: 600px) {
    .filmContainer {
        flex-direction: column;
        width: 100%;
    }

    .imgContainer {
        -webkit-clip-path: none;
                clip-path: none;
        flex: 1 1;
        height: 400px;
    }

    .filmInfo {
        align-items: flex-start;
        flex: 1 1;
        text-align: left;
        padding: 30px 20px;
    }

        .filmInfo .tagsCont,
        .filmInfo p {
            width: 100%;
        }

      form input {
          width: 200px;
      }
}

